import React from 'react';

import styles from './style/index.less';

// import Highcharts from 'highcharts'
// import { Divider } from 'antd';


// 时间




export default class extends React.Component {
  onClose() {
    console.log('onClose');
  }
  handleClick = () => {
    this.props.dispatch({
      type: 'pageReact/setOpened',
      payload: {},
    });
  };
  handleClick2 = () => {
    this.props.dispatch({
      type: 'pageReact2/setOpened',
    });
  };
  componentDidMount() {
    // 图表配置
    var options = {
      chart: {
        type: 'pie',
        options3d: {
          enabled: true,
          alpha: 60,
          beta: 0
        },
        backgroundColor: '#162063'
      },
      title: {
        text: ''
      },
      credits: {// 关闭版权信息的标签
        enabled: false
      },
      tooltip: {
        pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
      },
      plotOptions: {
        pie: {
          allowPointSelect: true,
          cursor: 'pointer',
          depth: 55,
          dataLabels: {
            enabled: false,
            format: '{point.name}'
          }
        }
      },
      series: [{
        type: 'pie',
        name: '',
        size:'120%',
        colors: ['#3734E3','#2DA3E0'],
        data: [
          ['终端占比',  39.0],
          ['终端占比',  61.0],
        ]
      }]
    };
    // 图表初始化函数
    var chart = Highcharts.chart('container', options);
    console.log('componentDidMount');
  }
  componentWillReceiveProps(nextProps) {
    console.log('componentWillReceiveProps');
    console.log(nextProps.toolParams);
  }
  componentWillUnmount() {
    console.log('componentWillUnmount');
  }
  render() {
    return (
       <div className={styles.fenxi}>
         <div className={styles.tiele} style={{
          borderImage: "url('/images/imgs/onescreen/标题框.png') 0 0 0 0 fill round",
          borderImageRepeat: 'stretch'}}> 终端占比分析</div>

          <div className={styles.kuang}  style={{
          borderImage: "url('/images/imgs/onescreen/框.png') 0 0 0 0 fill round",
          borderImageRepeat: 'stretch'}}>

            <div id="container" className={styles.container}></div>
            <div className={styles.labeltextleft}>39%</div>
            <div className={styles.labeltextright}>61%</div>
          </div>
            {/* 右边数据显示 */}
          <div className={styles.righttbox}>
              <div className={styles.xian} style={{
            borderImage: "url('/images/imgs/终端占比点-长.png') 0 0 0 0 fill round",
            borderImageRepeat: 'stretch',
          }}>
              </div>
              <div className={styles.text}>
                <span>30吨以上终端</span>
                <p>132&nbsp;<i>个</i></p>
              </div>
        </div>
          {/* 左边 */}
        <div className={styles.leftbox}>
            
              <div className={styles.text}>
                <span>30吨以下终端</span>
                <p>204&nbsp;<i>个</i></p>
              </div>

              <div className={styles.xian} style={{
            borderImage: "url('/images/imgs/终端占比点-短.png') 0 0 0 0 fill round",
            borderImageRepeat: 'stretch',
          }}>
              </div>
        </div>
       </div>
    ); 
  }
}
